.poke-database {
    top: calc(33.34vh - 1.67px);
}

.pokedex-container {
    float: right;
    margin-right: 5px;
    width: 85vw;
    height: calc(100vh - 10px);
    z-index: 5;
    position: relative;
}

.pokedex-title-list {
    height: 40px;
    margin: 10px;
    background: #ee1515;
    color: white;
    border-radius: 5px;
    text-align: center;
    font-size: 2em;
    line-height: 1.4em;
    box-shadow: inset 0px 0px 15px darkred;
    padding-top: 4px;
}

.pokedex-sorter {
    margin: 10px;
    height: 30px;
    display: flex;
}

.pokedex-search {
    width: calc(50% - 10px);
    height: inherit;
    border-radius: 5px;
    padding-left: 10px;
}

.pokedex-sort-by {
    width: calc(50% - 5px);
    margin-left: 5px;
    border-radius: 5px;
    background: #eeeeee;
    display: flex;
    justify-content: space-evenly;
}

.sort-by {
    width: calc(50% - 10px);
    height: 100%;
    box-shadow: 0px 0px 5px black;
    border-radius: 5px;
    text-align: center;
    overflow: hidden;
    margin-top: 2px;
    line-height: 2em;
    background: #eeeeee;
}

.sort-by:hover {
    box-shadow: 0px 0px 10px black;
    cursor: pointer;
    transform: scale(1.1);
}

.pokedex-type-filters {
    min-height: 30px;
    display: flex;
    background: #eeeeee;
    border-radius: 5px;
    margin: 15px 10px 10px 10px;
    width: calc(100% - 40px);
    padding: 10px;
    box-shadow: inset 0px 0px 5px #000000;
}

.filter-container {
    height: 24px;
    margin-top: 3px;
    border-radius: 100%;
    width: 24px;
    margin-left: calc(5.26% - 22.73px);
}

.filter-container:hover {
    filter: saturate(200%);
    transform: scale(1.1);
    cursor: pointer;
}

.filter-icon {
    height: 60%;
    width: 60%;
    margin: 20%;
}

.pokedex-list {
    overflow: auto;
    margin: 10px;
    height: 100%;
    width: calc(100% - 20px);
    max-height: calc(100% - 180px);
    border-radius: 3px;
    box-shadow: inset 0px 0px 5px #000000;
}

.scroller::-webkit-scrollbar {
    background-color: #fff;
    width: 6px;
}

.scroller::-webkit-scrollbar-track {
    background-color: #888888;
}

.scroller::-webkit-scrollbar-track:hover {
    background-color: #666666;
}

.scroller::-webkit-scrollbar-thumb {
    background-color: #ee1515;
    border-radius: 5px;
    border: 1px solid #fff
}

.scroller::-webkit-scrollbar-thumb:hover {
    background-color: #b31616;
    border: 1px solid #f4f4f4
}

.scroller {
    overflow-y: scroll;
    overflow-x: hidden
}