.pokemon-name {
    width: calc(100% - 30px);
    padding: 10px;
    font-size: 16px;
    font-family: inherit;
    box-shadow: 0px 0px 5px black;
    border-radius: 5px;
    margin: 5px;
    text-align: center;
}

.pokemon-details {
    width: calc(100% - 20px);
    box-shadow: 0px 0px 5px black;
    margin: 5px;
    border-radius: 5px;
    padding: 5px;
    background-size: 500% 500%;
    animation: gradient 15s ease infinite;
}

.pokemon-image {
    border-radius: 5px;
    margin: 5px;
    height: 35vh;
    width: 35vh;
    filter: drop-shadow(4px 4px 10px black);
}

.pokemon-stats {
    background: white;
    border-radius: 5px;
    margin: 15px;
    height: 35vh;
    flex-grow: 1;
    display: flex;
}

.pokemon-type {
    width: calc(17.5vh - 15px);
    font-size: 2em;
    margin: 5px;
    color: white;
    text-align: center;
    filter: drop-shadow(2px 2px 4px black);
}

.pokemon-type-container {
    border-radius: 5px;
    filter: drop-shadow(2px 2px 4px black);
}

.type-container {
    display: flex;
    justify-content: space-between;
    width: 35vh;
    margin: 5px;
}
.pokemon-power {
    height: calc(100% - 10px);
    width: 50px;
    margin: 5px;
    border-radius: 5px;
    background: #eeeeee;
    position: relative;
}

.power-info {
    border-radius: 5px;
    margin-top: 5px;
    color: white;
    text-align: center;
}

.power-base {
    height: calc(100% - 2em);
    border-radius: 5px;
    box-shadow: inset 0px 0px 5px black;
}

.power-fill {
    margin-left: 10%;
    width: 80%;
    text-align: center;
    color: white;
    position: absolute;
    bottom: calc(5px + 2em);
    border-radius: 5px;
}

.fill-bar-blocker {
    animation: fill 1s;
    width: 100%;
    height: 0%;
}

@keyframes fill {
    0% {
        height: 100%;
    }
    100% {
        height: 0%;
    }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}