body {
    overflow: hidden;
    font-family: monospace;
}


.main-page {
    position: absolute;
    z-index: 2;
    height: 100%;
    width: 100%;
}

@keyframes login-hover {
    0% {
        background: #ee1515;
    }
    100% {
        background: #770b0b;
    }
}
@keyframes login-unhover {
    0% {
        background: #770b0b;
    }
    100% {
        background: #ee1515;
    }
}

@keyframes register-hover {
    0% {
        background: #ffffff;
    }
    100% {
        background: #808080;
    }
}
@keyframes register-unhover {
    0% {
        background: #808080;
    }
    100% {
        background: #ffffff;
    }
}
@keyframes top-out {
    0% {
        top: 0vh;
    }
    100% {
        top: -100vh;
    }
}
@keyframes bot-out {
    0% {
        bottom: 0vh;
    }
    100% {
        bottom: -100vh;
    }
}
@keyframes main-panel-out {
    0% {
        top: 0vh;
    }
    100% {
        top: -100vh;
    }
}

.main-panel-cluster {
    width: 100vw;
    height: 50vh;
    position: relative;
}
.main-panel-cluster.login {
    background: #ee1515;
    top: 0vh;
}
.main-panel-cluster.register {
    background: #f0f0f0;
    bottom: 0vh;
}

.menu-form {
    border-radius: 10px;
    width: 66.67%;
    position: absolute;
    left: 16.67%;
    box-sizing: border-box;
    box-shadow: inset 0px 0px 30px black;
}
.menu-form.login {
    border: 5px solid white;
    bottom: 20px;
    opacity: 0;
}
.menu-form.register {
    border: 5px solid black;
    top: 20px;
    opacity: 0;
}
.menu-input {
    width: calc(80% + 10px);
    height: 2em;
    padding-left: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px black;
    margin-left: calc(10% - 10px);
}

.form-boundary {
    width: 100%;
    height: inherit;
    position: absolute;
    /* left: 25%; */
}
.form-boundary.login {
    bottom: 0%;
}
.form-boundary.register {
    top: calc(0% + 20px);
}
.form-label {
    text-align: center;
    width: 100%;
    margin: 10px 0px 10px 0px;
    color: white;
    font-size: 16px;
    display: block;
}

.circle-container {
    position: absolute;
    top: calc(50% - 25vw);
    left: calc(50% - 25vw);
    z-index: 2;
    width: 50vw;
    height: 50vw;
}
.top-circle-container {
    width: 100%;
    height: calc(50% - 5px);
    position: absolute;
    top: 0px;
}
.top-circle-container.out {
    animation: top-out 1.5s;
}
.bot-circle-container {
    width: 100%;
    height: calc(50% - 5px);
    position: absolute;
    bottom: 10px;
}
.bot-circle-container.out {
    animation: bot-out 1.5s;
}
.circle {
    height: 25vw;
    border: 20px solid #222224;
    background: #f0f0f0;
    position: relative;
    z-index: 2;
}
.circle.top {
    border-radius: 75vh 75vh 0 0;
    border-bottom: 0px;
    bottom: 5px;
}
.circle.bot {
    border-radius: 0 0 75vh 75vh;
    border-top: 0px;
}
.line {
    color: black;
    width: 100vw;
    position: absolute;
    height: 20px;
    left: -25vw;
    border-top: 20px solid #222224;
    z-index: 1;
}
.line.top {
    bottom: -15px;
}
.line.bot {
    top: 0px;
}

h3 {
    padding: 0px;
    margin: 10px;
    text-align: center;
    color: white;
}
hr {
    margin: 5px;
    padding: 0px;
    background-color: white;
    height: 1px;
}
.form-button {
    width: 20%;
    min-width: 30px;
    height: 20px;
    border-radius: 5px;
    margin: 10px 10px 10px 0px;
    float: right;
    text-align: center;
    line-height: 1;
    font-size: 10px;
}