@keyframes fade-in-homepage {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.home-page-container {
    width: 100vw;
    height: 100vh;
    background: #ee1515;
    position: absolute;
    top: 0px;
    z-index: 1;
    animation: fade-in-homepage 0.7s;
    opacity: 1;
}
.loader-background {
    background: black;
    opacity: 0.3;
    width: 100vw;
    height: 100vh;
    z-index: 500;
    position: absolute;
}
.loader-gif {
    background: url(../../assets/loading.gif) 0px 0px / 50px 50px no-repeat;
    width: 50px;
    height: 50px;
    position: absolute;
    top: calc(50vh - 25px);
    left: calc(50vw - 25px);
    z-index: 501;
}

@keyframes display-tab {
    0% {
        left: -100vw;
    }
    100% {
        left: -10vw;
    }
}

.contents-container {
    position: absolute;
    top: 5px;
    left: -100vw;
    width: 100vw;
    height: 100vh;
}

.tabs {
    position: relative;
    width: 100vw;
    padding-left: 30px;
}

.image-tab {
    height: calc(33.34vh - 6.67px);
    background: white;
    border-right: none;
    border-bottom-right-radius: 25px;
    border-top-right-radius: 25px;
    z-index: 2;
    box-shadow: 8px 10px 5px 0px black;
    border-left: 5vw solid white;
    position: relative;
    width: 100vw;
}

.hide-shadow {
    height: calc(33.34vh - 6.67px);
    background: white;
    border-right: none;
    z-index: 4;
    position: absolute;
    width: calc(100vw - 30px);
    right: 10px;
}

.contents {
    background: white;
    position: absolute;
    left: 5px;
    width: 100vw;
    height: 100vh;
    z-index: 3;
    top: 0;
    box-shadow: 8px 10px 5px 0px black;
    border-radius: 10px;
}

.contents-container.display {
    animation: display-tab 1s;
    left: -10vw;
}

