.portfolio-header {
    width: calc(100vw - 40px);
    height: 50vh;
    font-family: motion-picture;
    font-size: 20vh;
    text-align: center;
    color: white;
    filter: drop-shadow(0px 0px 10px black);
    line-height: 2.3em;
    border-bottom: 3px black solid;
    margin: 20px;
    box-shadow: 0px 0px 20px black;
    border-radius: 30px;
    cursor: pointer;
}